@import "../../styles/colorsConfig";
@import "../../styles/mixins";

div.mood_section {
  width: 100%;
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-end;
  padding: 5vh 0;
  z-index: 500;
  background-color: $primary-white;

  & > div.clear_white {
    width: 100%;
  }

  & > div.center_carousels {
    width: 100%;
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;

    & > div.carousels_wrapper {
      width: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }

  & > div.pop_up_wrapper {
    height: 100%;
  }
}

